   <div>
    <skeleton :nums="3" v-if="isSkeletonLoading"/>
    <div class="content" v-else>
      <p class="title-bg" :style="{opacity: titleOpacity}"><span>好物拼团</span></p>
      <img class="top-bg" src="//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/groupBooking/bg-group-top.png"/>
      <p class="icon-back"><img src="//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/common/back_white.png" @click="doBack"/></p>
      <div
        v-for="(prod, i) in groupList"
        :key="i"
        class="booking-item"
        @click="goGoodsDetail(prod)">
        <img :src="prod.picture"/>
        <div class="right-box">
          <div class="prod-title">{{prod.productName}}</div>
          <div class="sale-point">
            <ul  v-if="prod.sellPoint.length">
              <li v-for="(item,index) in prod.sellPoint" :key="index">{{item}}</li>
            </ul>
          </div>
          <!-- <div class="goods-tag" v-if="prod.tags && prod.tags.length">
            <ul>
              <li v-for="(item) in sortList(prod.tags)" :key="item.id">
                <template v-if="item.tagType === 2">
                  <van-tag :color="item.tagFontColour" :plain="item.tagFontStyle == '1'?false:true" :text-color="item.tagFontStyle == '1'?`#fff`:item.tagFontColour">{{item.tagName}}</van-tag>
