                  <img :src="item.tagUrl | imageToHttps | imageToWebp($webpSurpport)" alt />
                </template>
              </li>
            </ul>
          </div> -->
          <div class="origin-price">
            <span class="tag">￥</span>
            <span class="price-int">
              {{prod.basePrice | formatPrice | formatPriceInt}}
            </span>
            <span class="tag">
              {{prod.basePrice | formatPrice | formatPriceFloat}}
            </span>
          </div>
          <div class="group-price">
            <span class="tag">团购价：￥</span>
            <span class="price-int">
              {{prod.activityPrice | formatPrice | formatPriceInt}}
            </span>
            <span class="tag" style="position: relative; top: .01rem;">
              {{prod.activityPrice | formatPrice | formatPriceFloat}}
            </span>
          </div>
          <div class="shop-row" @click.stop="goShop(prod)">
            <div class="shop-name">{{prod.storeName}}</div>
            <span>进店<van-icon name="arrow" size=".24rem" color="#333330"/></span>
          </div>
          <div class="button">
            <span>立即抢购</span>
          </div>
        </div>
        <div class="book-num">
          <span>{{prod.clustersNum}}人团</span>
        </div>
      </div>
      <div style="height: .24rem"></div>
    </div>
  </div>
</template>
<script>
/**
 * 拼团列表
 */
import skeleton from '@/components/skeleton'
import {
  getMainGroupBooking
} from '@/http/service/groupBooking'
import { mapGetters } from 'vuex'
export default {
  components: {
    skeleton,
  },
  data() {
    return {
      isSkeletonLoading: true,
      groupList: [],
      titleOpacity: 0,
    }
  },
  // created() {
  //   },
  mounted() {
    this.getData();
  },
  destroyed() {
    document.getElementById('app-warp').removeEventListener('scroll', this.bindScroll);
  },
  computed: {
    ...mapGetters({
      location: 'getlocationInfo',
    })
  },
  methods: {
    doBack() {
      this.$store.dispatch('doHistoryBack');
    },
    bindScroll(ev) {
      let scrollTop = ev.target.scrollTop
      this.titleOpacity = scrollTop / 200;
    },
    getData() {
      getMainGroupBooking({ streetId: this.location.townshipId }).then(res => {
        this.isSkeletonLoading = false;
        if (res.success) {
          res.data.map(item => {
            item.sellPoint = item.sellPoints != null ? item.sellPoints.split(',') : []
          })
          this.groupList = res.data;
          this.$nextTick(() => {
            document.getElementById('app-warp').addEventListener('scroll', this.bindScroll);
          })
        } else {
          this.$toast(res.message);
        }
      })
    },
    goShop(prod) {
      this.$router.push(`/shopDetail?storeId=${prod.storeId}`)
    },
    goGoodsDetail(prod) {
      this.$router.push(`/goodsDetail?productId=${prod.productId}&visitStore=${prod.storeId}`)
    },
    /**
     * 标签排序
     */
    sortList(arr) {
      let len = arr.length;
      for (var i = 0; i < len; i++) {
        for (var j = 0; j < len - 1 - i; j++) {
          if (arr[j].tagOrder > arr[j + 1].tagOrder) {
            var temp = arr[j + 1]
            arr[j + 1] = arr[j]
            arr[j] = temp
          }
        }
      }
      return arr
    }
  }
}
</script>
